@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/opensans/opensans-300.woff2") format("woff2"), url("../fonts/opensans/opensans-300.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans/opensans-400.woff2") format("woff2"), url("../fonts/opensans/opensans-400.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/opensans/opensans-600.woff2") format("woff2"), url("../fonts/opensans/opensans-600.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensans/opensans-700.woff2") format("woff2"), url("../fonts/opensans/opensans-700.woff") format("woff"); }

/* -- ОБЩИЕ -- */
*,
*::after,
*::before {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  letter-spacing: -0.005em;
  background-color: #ffffff; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0; }

.nowrap {
  white-space: nowrap; }

/* -- CONTAINER -- */
.container {
  max-width: 1200px;
  padding: 0 30px;
  margin: 0 auto;
  /* -- TABLET -- */
  /* -- MOBILE -- */ }
  @media (max-width: 1023px) {
    .container {
      max-width: 768px;
      padding: 0 0;
      margin: 0 auto; } }
  @media (max-width: 767px) {
    .container {
      max-width: 320px;
      padding: 0 30px;
      margin: 0 auto; } }

/* -- HEADER -- */
.header {
  min-height: 825px;
  background: #000000 url("../img/header_bg_desktop_1x.jpg") no-repeat;
  background-size: cover;
  /* -- RETINA -- */ }
  @media (min-resolution: 1.5dppx) {
    .header {
      background-image: url("../img/header_bg_desktop_2x.jpg"); } }

.header__top-inner {
  min-height: 65px;
  padding-left: 133px;
  padding-right: 133px; }

.header__site-nav {
  margin: 0; }

.header__bottom-inner {
  display: flex; }

.header__page-title {
  margin: 0;
  font-weight: 700;
  font-size: 46px;
  line-height: 64px;
  color: #ffffff; }

.header__slogan {
  margin: 0;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  color: rgba(255, 255, 255, 0.8); }

/* -- ГЛАВНАЯ НАВИГАЦИЯ -- */
.site-nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0;
  padding-top: 22px;
  padding-left: 19px;
  padding-right: 22px;
  padding-bottom: 10px;
  list-style: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.site-nav a {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  letter-spacing: -0.005em;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  color: rgba(255, 255, 255, 0.7); }
